import React from "react";
import { Progress } from "antd";

const Analytics = ({ allTransaction }) => {
  // category
  const categories = [
    "salary",
    "tip",
    "freelance",
    "taken",
    "food",
    "travel",
    "bill",
    "fee",
    "medical",
    "given",
  ];
  //total transaction
  const totalTransaction = allTransaction.length;

  const totalIncomeTransaction = allTransaction.filter(
    (transaction) => transaction.type === "income"
  ).length;
  const totalExpenseTransaction = allTransaction.filter(
    (transaction) => transaction.type === "expense"
  ).length;

  const totalIncomePercentage =
    (totalIncomeTransaction / totalTransaction) * 100;
  const totalExpensePercentage =
    (totalExpenseTransaction / totalTransaction) * 100;

  //total turnover
  const totalTurnover = allTransaction.reduce(
    (acc, transaction) => acc + transaction.amount,
    0
  );

  const totalIncomeTurnover = allTransaction
    .filter((transaction) => transaction.type === "income")
    .reduce((acc, transaction) => acc + transaction.amount, 0);
  const totalExpenseTurnover = allTransaction
    .filter((transaction) => transaction.type === "expense")
    .reduce((acc, transaction) => acc + transaction.amount, 0);

  const totalIncomeTurnoverPercent =
    (totalIncomeTurnover / totalTurnover) * 100;
  const totalExpenseTurnoverPercent =
    (totalExpenseTurnover / totalTurnover) * 100;

  return (
    <>
      <div className="row m-3">
        <div className="col-md-3">
          <div className="card">
            <div className="card-head">
              <h4 className="d-flex justify-content-center">Total Transaction : {totalTransaction}</h4>
            </div>
            <div className="card-body">
              <h5 className="text-success">
                Income : {totalIncomeTransaction}
              </h5>
              <h5 className="text-danger">
                Expense : {totalExpenseTransaction}
              </h5>
              <div>
                <Progress
                  type="circle"
                  strokeColor="green"
                  className="mx-2"
                  percent={totalIncomePercentage.toFixed(0)}
                />
                <Progress
                  type="circle"
                  strokeColor="red"
                  className="mx-2"
                  percent={totalExpensePercentage.toFixed(0)}
                />
              </div>
            </div>
          </div>
        </div>
        <div className="col-md-3">
          <div className="card">
            <div className="card-head">
              <h4 className="d-flex justify-content-center" >Total Turnover : {totalTurnover}</h4>
            </div>
            <div className="card-body">
              <h5 className="text-success">
                Income Turnover : {totalIncomeTurnover}
              </h5>
              <h5 className="text-danger">
                Expense Turnover: {totalExpenseTurnover}
              </h5>
              <div>
                <Progress
                  type="circle"
                  strokeColor="green"
                  className="mx-2"
                  percent={totalIncomeTurnoverPercent.toFixed(0)}
                />
                <Progress
                  type="circle"
                  strokeColor="red"
                  className="mx-2"
                  percent={totalExpenseTurnoverPercent.toFixed(0)}
                />
              </div>
            </div>
          </div>
        </div>
        <div className="col-md-3">
          <h6 className="bg-dark p-2 text-light">Category-wise Income</h6>
          {categories.map((category) => {
            const amount = allTransaction
              .filter(
                (transaction) =>
                  transaction.type === "income" &&
                  transaction.category === category
              )
              .reduce((acc, transaction) => acc + transaction.amount, 0);

            return (
              amount > 0 && (
                <div className="card mt-2">
                  <div className="card-body">
                    <h6>{category}</h6>
                    <Progress
                      percent={((amount / totalIncomeTurnover) * 100).toFixed(
                        0
                      )}
                      showInfo={false}
                    />
                  </div>
                </div>
              )
            );
          })}
        </div>
        <div className="col-md-3">
          <h6 className="bg-warning p-2 text-light">Category-wise Expense</h6>
          {categories.map((category) => {
            const amount = allTransaction
              .filter(
                (transaction) =>
                  transaction.type === "expense" &&
                  transaction.category === category
              )
              .reduce((acc, transaction) => acc + transaction.amount, 0);

            return (
              amount > 0 && (
                <div className="card mt-2">
                  <div className="card-body">
                    <h5>{category}</h5>
                    <Progress
                      percent={((amount / totalExpenseTurnover) * 100).toFixed(
                        0
                      )}
                      strokeColor="red"
                      showInfo={false} 
                    />
                  </div>
                </div>
              )
            );
          })}
        </div>
      </div>
    </>
  );
};

export default Analytics;
